import { Autocomplete, TextField } from "@mui/material";
import { useState } from "react";
import { HeadLine, Text } from "../Component";

interface IValue {
  target: { value: string };
}
const suggestions = {
  where: [
    { label: "Hemma" },
    { label: "P jobbet" },
    { label: "I telefonsamtal" },
    { label: "P sms" },
    { label: "Hemma hos Malin" },
    { label: "I Berlin" },
    { label: "P tunnelbanan" }
  ],
  who: [{ label: "Mamma" }, { label: "Stefan" }, { label: "Busschauffren" }]
};

export function TheSituation() {
  const [where, setWhere] = useState("");
  const onWhereChange = ({ target: { value } }: IValue) => setWhere(value);
  const [who, setWho] = useState("");
  const onWhoChange = ({ target: { value } }: IValue) => setWho(value);
  const [happened, setHappened] = useState("");
  const onHappenedChange = ({ target: { value } }: IValue) => setHappened(value);
  return (
    <>
      <HeadLine>Vad har hnt?</HeadLine>
      <Text>
        Vlj ett minne, tnk tillbaka till en person, en plats. Det kan tex ha hnt igr, hemma, nr din sambo skrek p
        dig, eller 12 r sedan nr du blev borttappad av ditt syskon.
      </Text>

      <Autocomplete
        freeSolo
        id="where"
        options={suggestions.where}
        sx={{ width: 300 }}
        renderInput={(params) => <TextField {...params} label="Jag var" onChange={onWhereChange} />}
      />
      <Autocomplete
        freeSolo
        id="who"
        options={suggestions.who}
        sx={{ width: 300 }}
        renderInput={(params) => <TextField {...params} label="med" onChange={onWhoChange} />}
      />
      <TextField label="Beskriv vad som hnde" sx={{ width: 300 }} onChange={onHappenedChange} />
    </>
  );
}
